}

.categories-2 {
  flex-direction: column;
  align-items: flex-start;
  gap: 24px;
  margin-top: 40px;
  margin-left: auto;
  margin-right: auto;
  display: flex;
  position: relative;
  overflow-y: auto;
}

.categories-title {
  color: #1119;
