  line-height: 24px;
}

.categories-list {
  width: 100%;
  margin: 48px 0 0;
  padding: 8px 0 10px;
  list-style: none;
  overflow: auto;
}

.categories-el, .categories-btn, .categories-list li {
  color: #1119;
  letter-spacing: -.32px;
  white-space: normal;
  text-overflow: ellipsis;
  cursor: pointer;
  justify-content: flex-start;
  align-items: center;
  gap: 8px;
  max-width: 100%;
  font-family: DMSans-400;
  font-size: 16px;
  font-style: normal;
  font-weight: 400;
  line-height: 18px;
  list-style: none;
  transition: color .3s, font-size .3s;
  display: flex;
  overflow: hidden;
}

.categories-el:hover, .categories-btn:hover, .categories-list li:hover {
  color: #4f2ee8;
  letter-spacing: -.36px;
  text-transform: uppercase;
  font-family: DMSans-700;
  font-size: 18px;
  font-style: normal;
  line-height: 24px;
}

.categories-btn {
  z-index: 1;
  position: absolute;
  top: 0;
  left: 0;
}

@media (min-width: 320px) and (max-width: 767px) {
