.swiper-slide:before {
  counter-increment: section;
  content: "0" counter(section) " ";
  color: #fff;
  font-family: DMSans-400;
  font-size: 14px;
  font-weight: var(--regular);
  letter-spacing: -.56px;
  line-height: 1.29;
}

.swiper {
  height: 188px;
  margin-bottom: 48px;
  overflow: hidden;
}

.swiper-wrapper {
  counter-reset: section;
  width: 100%;
  height: 100%;
  margin-bottom: 16px;
  list-style-type: none;
}

.swiper-slide {
  align-items: center;
  gap: 16px;
  width: 50%;
  font-family: DMSans-400;
  font-size: 18px;
  display: flex;
}

