  max-width: -moz-fit-content;
  max-width: fit-content;
  max-height: 100%;
}

.swiper-button-next, .swiper-button-up {
  z-index: 0;
  background-color: #fff;
  border: none;
  border-radius: 38px;
  outline: none;
  justify-content: center;
  align-items: center;
  width: 32px;
  height: 32px;
  display: flex;
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
}

.swiper-button-next-icon, .swiper-button-up-icon {
  fill: #4f2ee8;
}

.swiper-button-next-icon {
  transform: rotate(180deg)translateY(-105%)translateX(-105%);
}

.swiper-button-up.is-hidden {
  display: none;
}

